.dropdown-divider {
    height: 0 !important;
    margin: 0.5rem 0 !important;
    overflow: hidden !important;
    border-top: 1px solid #d9dee3 !important;
}

.menu-header {
    &::before {
        content: "";
        position: absolute;
        left: 0;
        top: 50%;
        width: 1rem;
        height: 1px;
        transition: all 0.3s ease-in-out;
        transform: translateY(-50%);
        left: -1.5rem;
    }

    position: relative;
    margin-top: .5rem;
}